:root {
  color: #242424;
  font-synthesis: none;
  text-rendering: optimizelegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
  background-color: rgba(255, 255, 255, .87);
  font-family: Inter, Avenir, Helvetica, Arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.container {
  margin: 0 auto;
  padding: 0 15px;
}

@media screen and (min-width: 320px) {
  .container {
    max-width: 100%;
    margin: 0 auto;
    padding: 0 15px;
  }

  @media screen and (min-width: 320px) and (max-width: 767px) {
    .container {
      width: 320px;
      padding: 0 20px;
    }
  }

  @media screen and (min-width: 768px) and (max-width: 1279px) {
    .container {
      width: 768px;
      padding: 0 32px;
    }
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding: 0 32px;
  }
}

html {
  box-sizing: border-box;
}

*, :after, :before {
  box-sizing: inherit;
}

body, h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

h1, p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

@font-face {
  font-family: Roboto Bold;
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local(Roboto Bold), url("Roboto-Bold.7e5c49f0.eot") format("eot");
  src: local(Roboto Bold), url("Roboto-Bold.31852a30.ttf") format("truetype");
  src: local(Roboto Bold), url("Roboto-Bold.b47474ed.woff") format("woff");
  src: local(Roboto Bold), url("Roboto-Bold.64484150.woff2") format("woff2");
}

@font-face {
  font-family: Roboto Medium;
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: local(Roboto Medium), url("Roboto-Medium.790ca77e.eot") format("eot");
  src: local(Roboto Medium), url("Roboto-Medium.7301597e.ttf") format("truetype");
  src: local(Roboto Medium), url("Roboto-Medium.3a60aea3.woff") format("woff");
  src: local(Roboto Medium), url("Roboto-Medium.8036abf1.woff2") format("woff2");
}

@font-face {
  font-family: Roboto Regular;
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local(Roboto Regular), url("Roboto-Regular.d2fb154c.eot") format("eot");
  src: local(Roboto Regular), url("Roboto-Regular.0dc49cf8.ttf") format("truetype");
  src: local(Roboto Regular), url("Roboto-Regular.11c4383a.woff") format("woff");
  src: local(Roboto Regular), url("Roboto-Regular.31550cf1.woff2") format("woff2");
}

.is-hidden {
